export default {
	components:{
		swiperImage,
		price,
		baseInfo,
		uParse,
		card,
		bottomBtn,
		commonPopup,
		scradioGroup,
		uniNumberBox,
		uniListItem
		// uniListItem,
		// scrollComment
	},
	
	data() {
		return {
			goods:'',
			selects:[
				{	
					title:"颜色",
					selected:0,
					list:[
						{name:"蓝色"},
						{name:"红色"},
						{name:"黄色"}
					]
				
				},
				{
